import React from 'react'
import './experience.css'
import {BsFillBookmarkCheckFill} from 'react-icons/bs';
import {SiJava} from 'react-icons/si';
import {FaReact} from 'react-icons/fa';
import {ImHtmlFive2} from 'react-icons/im';
import {DiCss3} from 'react-icons/di';
import {SiJavascript} from 'react-icons/si';
import {SiMysql} from 'react-icons/si';
import {FaPython} from 'react-icons/fa';

import Paint from '../../assets/PaintProgram.png'
const Experience = () => {
  
  return (
    <section id ='experience'>
        <h5> What Skills I Have</h5>
        <h2> My Experience</h2>
        <div className='container experience_container'>
            <div className = 'experience front_end'>
                <h6> Frontend Development</h6>
                <div className='experience_content'>
                    <article className = 'experience_details'>
                        <FaReact className = 'experience_details-icon'/>
                        <div>
                            <h4>React</h4>
                            <small className = 'text-light'>Intermediate</small>
                        </div>
                        <ImHtmlFive2 className = 'experience_details-icon'/>
                        <div>
                          <h4>HTML</h4>
                          <small className = 'text-light'>Intermediate</small>
                        </div>
                        <DiCss3 className = 'detail'/>
                        <div>
                          <h4>CSS</h4>
                          <small className = 'text-light'>Intermediate</small>
                        </div>
                        <SiJavascript className = 'experience_details-icon'/>
                        <div>
                          <h4>JavaScript</h4>
                          <small className = 'text-light'>Intermediate</small>
                        </div>
                    </article>
                </div>
            </div>
            <div className = 'experience back_end'>
            <h6> Backend Development</h6>
                <div className='experience_content'>
                    <article className = 'experience_details'>
                        <SiJava className = 'experience_details-icon'/>
                        <div>
                          <h4>Java</h4>
                          <small className = 'text-light'>Experienced</small>
                        </div>
                        <SiMysql className = 'detail'/>
                        <div>
                          <h4>MySQL</h4>
                          <small className = 'text-light'>Intermediate</small>
                        </div>
                        <FaPython className = 'experience_details-icon'/>
                        <div>
                          <h4>Python</h4>
                          <small className = 'text-light'>Intermediate</small>
                        </div>
                        <BsFillBookmarkCheckFill className = 'experience_details-icon'/>
                        <div>
                          <h4>C</h4>
                          <small className = 'text-light'>Intermediate</small>
                        </div>
                        <BsFillBookmarkCheckFill className = 'experience_details-icon'/>
                        <div>
                          <h4>XML</h4>
                          <small className = 'text-light'>Intermediate</small>
                        </div>
                    </article>
            </div>

        </div>
        </div>

    </section>
  )
}

export default Experience